.custom-select {
    position: relative;
    background: #ffffff;
    box-shadow: 0px 10px 40px rgba(193, 195, 222, 0.25);
    border-radius: 53px;
    height: 51px;
    width: 295px;

    border: none;

    font-family: Gotham Pro;
    font-size: 14px;
    line-height: 21px;
    text-align: left;

    padding: 0;
    z-index: 3;
}




.custom-select__selected-option {
    /*padding: 15px 10px 10px 25px;*/
    position: relative;
    z-index: 5;
    /* background: #ffffff; */
    border-radius: 0;
    /*border-radius: 28px 28px;*/
    overflow: auto;
    height: 24px;
    width: 70%;
    margin: 15px 10px 7px 26px;

    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background: inherit;
}

.custom-select__drop-down-button {
    position: absolute;
    right: 28px;
    top: 16px;
}

.custom-select__drop-down-list {
    background: #ffffff;
    list-style-type: none;
    border-radius: 0 0 28px 28px;
    padding: 16px 0 0 0;
    box-shadow: 0px 40px 40px rgba(193, 195, 222, 0.25);
    /* max-height: calc(36px * (10%) + 18px); */
    overflow: auto;
    margin-top: -18px;
    z-index: 2;
}

.custom-select__option {
    background: #ffffff;
    padding: 0px 10px 15px 26px;
}

.custom-select__option:first-child {
    padding-top: 22px;
    margin-top: -17px;
}
/* .custom-select__option:nth-child(4) {
    padding-top: 22px;
    margin-top: -17px;
    background: green    
} */

.custom-select__option:last-child {
    border-radius: 0 0 28px 28px;
}

.disabled-select, .disabled-select * {
    background: #ced4da;
}

